<template>
    <div class="payRecord">
		<div class="top-head">
			<div class="main-item">
				<i class="bbx-font mainFontColor bbx-icon-weibiaoti--30"></i>
				<div class="info">
					<p class="p">{{ $L('可用的余额') }}</p>
					<p class="font-large">{{ balance }}</p>
				</div>
			</div>
			<div class="main-item">
				<i class="bbx-font mainFontColor bbx-icon-weibiaoti--30"></i>
				<div class="info">
					<p class="p">{{ $L('历史累计金额') }}</p>
					<p class="font-large">{{ allIncome }}</p>
				</div>
			</div>
			<div class="right-btn">
				<router-link :to="{ 'name':'payCharge'}">
					<el-button type="primary">{{ $L('立即充值') }}</el-button>
				</router-link>
				<el-button @click="goHome" class="btn mainFontColor" >{{ $L('马上购物') }}</el-button>
			</div>
		</div>
		<div class="content-middle">
			<div class="left">
				{{ $L('交易记录') }}
			</div>
			<div class="right">
				<el-select class="search" v-model="messageType" v-bind:size="btnSize"
						   @change="changeTypeFunc">
					<el-option
							v-for="item in messageTypelist"
							:key="item.type"
							:label="item.name"
							:value="item.type">
					</el-option>
				</el-select>
				<span>{{ $L('创建时间') }}：</span>
				<el-date-picker class="time-search"
								style="width: 350px;"
								v-model="SearchTimeZone"
								type="datetimerange"
								align="right"
								v-bind:size="btnSize"
								:start-placeholder="$L('开始日期')"
								:end-placeholder="$L('结束日期')"
								value-format="yyyy-MM-dd HH:mm:ss"
								:default-time="['00:00:00', '23:59:59']"
								@change="SearchTimeFunc">
				</el-date-picker>

			</div>
		</div>
		<div class="payrecordList" v-loading="loading">
			<el-table :data="tableData.list"
					  :empty-text="$L('暂无数据')"
					  :header-cell-style="{background:'#f0f1f5',padding:'10px',color:'#666'}"
					  style="width: 100%" class="table">
				<el-table-column
						prop="name"
						:label="$L('名称')"
						align="left"
						min-width="180">
					<div slot-scope="scope" class="info">
						<template v-if="scope.row.orderType == 1">
							<p class="name">{{ scope.row.goodsName }}</p>
							<p class="orderColor">{{ $L('订单号') }}：{{ scope.row.orderSn }}</p>
						</template>
						<template v-else-if="scope.row.orderType == 2">
							<p class="name">{{ $L('退款订单')}}</p>
							<p class="orderColor">{{ $L('订单号') }}：{{ $L('自动退款没有订单号') }}</p>
						</template>
						<template v-else-if="scope.row.orderType == 3">
							<p class="name">{{ $L('录款')}}</p>
							<p class="orderColor">{{ $L('订单号') }}：{{ $L('管理员手动录款没有订单号') }}</p>
						</template>
						<template v-else-if="scope.row.orderType == 4">
							<p class="name">{{ $L('管理员扣款')}}</p>
							<p class="orderColor">{{ $L('订单号') }}：{{ $L('管理员手动扣款没有订单号') }}</p>
						</template>
						<template v-else-if="scope.row.orderType == 5">
							<p class="name">{{ $L('在线充值')}}</p>
							<p class="orderColor">{{ $L('订单号') }}：{{ $L('在线充值金额不生成订单号') }}</p>
						</template>
						<template v-else>
							<p class="name">{{ scope.row.goodsName }}</p>
							<p class="orderColor">{{ $L('订单号') }}：{{ scope.row.orderSn }}</p>
						</template>
					</div>
				</el-table-column>
				<el-table-column
						prop="createTime"
						:label="$L('创建时间')"
						align="center" min-width="140">
					<div slot-scope="scope">
						{{ scope.row.addTime }}
					</div>
				</el-table-column>
				<el-table-column
						prop="price"
						:label="$L('金额')"
						align="center" min-width="90">
					<div slot-scope="scope" class="success">
						<span class="font-green">{{ scope.row.amount }}</span>
					</div>
				</el-table-column>
				<el-table-column
						prop="details"
						:label="$L('明细')"
						align="center" min-width="90">
					<div slot-scope="scope">
						{{ detailStatue(scope.row.payType).text }}
					</div>
				</el-table-column>
				<el-table-column
						prop="state"
						:label="$L('状态')"
						align="center" min-width="90">
					<div slot-scope="scope" class="success">
						<span v-bind:class="statusFunc(scope.row.status).color">{{ statusFunc(scope.row.status).text }}</span>
					</div>
				</el-table-column>
			</el-table>
			<!-- 分页 -->
			<div class="list-pages" v-if="tableData.list !== null && tableData.allNum>0">
				<el-pagination
						background
						@size-change="handleSizeChange"
						@current-change="handleCurrentChange"
						:current-page="currentPage"
						:page-size="pageSize"
						layout="prev, pager, next,  total, jumper"
						:total="tableData.allNum">
				</el-pagination>
			</div>
		</div>
	</div>
</template>

<script>
    export default {
        name: "payRecord",
		data(){
			return{
				loading: true,
				errorType: 'error',
				errorTitle: this.$L('出错了'),
				errorTips: null,
				errorShow: false,
				btnSize:'small',
				balance:'',
				allIncome:'',
				tableData:{},
				pageSize:10,
				currentPage:1,
				messageType:0,
				msgArr:[],
				topMenu :[
					{
						'groupTitle':'',
						'menuList':[
							{text: this.$L('信息发布')}
						]
					}
				],
				messageTypelist:[
					{
						type:0,
						name:this.$L('全部状态')
					},
					{
						type:1,
						name:this.$L('已确认')
					},
					{
						type:2,
						name:this.$L('未确认')
					},
				],
				SearchTimeZone:"",
				type:0,
				dtCreateStart:'',
				dtCreateEnd:''
			}
		},
		created () {
			this.getData();
		},
		methods : {
			detailStatue(value){
				let x;
				switch(value){
					case 1:
						x = {text:this.$L('管理员录款')};
						break;
					case 2:
						x = {text:this.$L('管理员扣款')};
						break;
					case 3:
						x = {text:this.$L('支付宝支付')};
						break;
					case 4:
						x = {text:this.$L('快钱支付')};
						break;
					case 5:
						x = {text:this.$L('财付通支付')};
						break;
					case 6:
						x = {text:this.$L('银行支付')};
						break;
					case 7:
						x = {text:this.$L('货到付款')};
						break;
					case 8:
						x = {text:this.$L('环球支付')};
						break;
					case 9:
						x = {text:this.$L('贝宝支付')};
						break;
					case 10:
						x = {text:this.$L('邮局汇款')};
						break;
					case 11:
						x = {text:this.$L('微信支付')};
						break;
					case 12:
						x = {text:this.$L('微信手机支付')};
						break;
					case 13:
						x = {text:this.$L('网银在线')};
						break;
					case 14:
						x = {text:this.$L('退货退款')};
						break;
					case 15:
						x = {text:this.$L('余额支付')};
						break;
					default:
						x = {text:this.$L('其他')};
						break;
				}
				return x;
			},
			showError(title, tips, type) {
				if (type === 'warning') {
					this.errorType = 'warning';
				} else {
					this.errorType = 'error';
				}
				if (title && title !== '') {
					this.errorTitle = title;
				} else {
					this.errorTitle = this.$L('出错了');
				}
				if (tips && tips !== '') {
					this.errorTips = tips;
				} else {
					this.errorTips = null;
				}
				this.loading = false;
				this.errorShow = true;
			},
			getData() {
				const _self = this;
				_self.loading = true;
				let arr = {
					p : _self.currentPage,
					num : _self.pageSize,
					type : _self.type,
					dtCreateStart : _self.dtCreateStart,
					dtCreateEnd : _self.dtCreateEnd
				};
				_self.$api('getAccountList',arr , function (reData) {
					if (reData.flag === 1) {
						_self.tableData = reData.data;
						_self.balance = reData.data.balance;
						_self.allIncome = reData.data.allIncome;

						_self.loading = false;
					} else if (reData.flag === -1) {
						//登录超时了
						_self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
							confirmButtonText: _self.$L('重新登录'),
							cancelButtonText: _self.$L('取消'),
							type: 'error',
							distinguishCancelAndClose: true
						}).then(() => {
							_self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
						}).catch(action => {
							_self.$gotoWebHome();
						});
					} else {
						//未知错误
						_self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
							confirmButtonText: _self.$L('点击重试'),
							cancelButtonText: _self.$L('返回首页'),
							type: 'error',
							distinguishCancelAndClose: true
						}).then(() => {
							_self.getData();
						}).catch(action => {
							_self.$router.push({name: 'home'});
						});
					}
				}, function (error) {
					//网络请求错误
					_self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
						confirmButtonText: _self.$L('点击重试'),
						cancelButtonText: _self.$L('返回首页'),
						type: 'error',
						distinguishCancelAndClose: true
					}).then(() => {
						_self.getData();
					}).catch(action => {
						_self.$router.push({name: 'home'});
					});
				});
			},
			handleCurrentChange(val){
				this.currentPage = val;
				this.getData();
			},
			handleSizeChange(val){
				this.pageSize = val;
				this.getData();
			},
			statusFunc(value){
				let x;
				switch(value){
					case 0:
						x = {text:this.$L('未确认'),color:'font-red'};
						break;
					default:
						x = {text:this.$L('已确认'),color:'font-green'};
						break;
				}
				return x;
			},
			changeTypeFunc(val){
				this.type = val;
				this.getData();
			},
			SearchTimeFunc(time){
				// dtCreateStart   dtCreateEnd
				if(time !== null){
					this.dtCreateStart = time[0];
					this.dtCreateEnd = time[1];
				}else{
					this.dtCreateStart = '';
					this.dtCreateEnd = '';
				}
				this.getData();
			},
			goHome(){
				this.$gotoWebHome();
			},
		}
    }
</script>

<style lang="scss" scoped>
	@import "../../../assets/css/common/common";
	@import "../../../assets/css/common/lib";
	.payRecord{
		padding: 20px;
		margin: 10px 0;
		.top-head{
			padding: 20px;
			background: #fff;
			.main-item{
				width: 300px;
				display: inline-block;
				.bbx-font{
					font-size: 46px;
					vertical-align: super;
					margin-right: 20px;
				}
				.info{
					display: inline-block;
					.p{
						font-size: 16px;
						color: #999;
					}
					.font-large{
						font-size: 26px;
					}
				}
			}
			.right-btn{
				display: inline-block;
				vertical-align: top;
				margin-top: 20px;
				margin-left: 5%;

				.el-button{
					width: 120px;
					&:last-child{
						margin-left: 30px;
					}
				}
			}
		}
		.content-middle{
			height: 32px;
			margin-top: 20px;
			.left{
				float: left;
				line-height: 32px;
				font-size: 16px;
			}
			.right{
				float: right;
				.time-search{
					vertical-align: bottom;
				}
				.font{
					padding-left: 20px;
					font-size: 16px;
				}
				.search{
					width: 120px;
					padding-right: 10px;
				}
				.input-with-select{
					width: calc(100% - 480px);
					padding-left: 10px;
				}
			}
		}
		.payrecordList{
			margin-top: 20px;
			position: relative;
			width:100%;
			height: auto;
			padding: 20px;background: #fff;
			.table{
				width: 100%;
				.info{
					.name{
						text-overflow: ellipsis;
						white-space: nowrap;
						overflow: hidden;
						font-size: 14px;
					}
				}
				.orderColor{
					color: #cccccc;
				}
				.success{
					.font-red{
						color: #FF0000;
					}
					.font-green{
						color: #00bf00;
					}
				}
			}
			.list-pages{
				text-align: center;
				padding: 40px 0 20px;
				background: #fff;
			}
			/deep/ .el-table__empty-block {
				@extend .no-select;
				position: relative;
				width: 100%;
				height: 380px;
				line-height: 380px;
				text-align: center;
				background-color: #ffffff;
				font-size: 16px;
				color: #999;
				margin-bottom: 20px;
			}
		}
		.error {
			position: absolute;
			z-index: 200;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background: #FFFFFF;
			overflow: hidden;

			.error-tips {
				position: absolute;
				top: 50%;
				left: 50%;
				width: 320px;
				transform: translate(-50%, -50%);
			}
		}
	}
</style>